<template>
  <v-container
    style="
      background-color: white;
      opacity: 0.9;
      margin-top: 50px;
      -webkit-box-shadow: 0px 0px 52px 11px rgba(0, 0, 0, 0.7);
      -moz-box-shadow: 0px 0px 52px 11px rgba(0, 0, 0, 0.7);
      box-shadow: 0px 0px 52px 11px rgba(0, 0, 0, 0.7);
    "
  >
    <h2 class="text-center titulos" style="font-size: 40px">
      Experiencia Laboral
    </h2>

    <v-row class="text-center">
      <v-col cols="12" v-for="experiencia in experiencias" :key="experiencia.id">
        <v-hover v-slot:default="{ hover }">
          <v-card
            :elevation="hover ? 24 : 2"
            :class="{ 'on-hover': hover }"
            class="mx-auto pb-4 mb-4"
            max-width="500"
          >
            <v-img
              height="300px"
              :src="require('../assets/experiencia/' + experiencia.avatar)"
            >
            </v-img>

