<script>
export default {
  name: "Inicio",
  data() {
    return {
      experiencias: [
        {
          id: 1,
          periodo: "Dic.2020 - presente",
          empresa: "Globant",
          proyectos:'Smartcall Messenger - Linkware',
          avatar: "Globant.png",
          show: false,
        },
        {
          id: 2,
          periodo: "Jul.2020 - Nov.2020",
          empresa: "UPSO",
          proyectos:'Capacitación framework SIU-Toba y Guaraní',
          avatar: "upso.jpg",
          show: false,
        },
     
      ],
      show: false,
    };
  },
};
</script>

<style>
.v-card__text,
.v-card__title {
  word-break: normal !important; /* maybe !important  */
}
.titulos {
  text-shadow: 4px 4px 8px #4e4e4e;
